'use strict';
var _self;
import { BOHAI ,RIGHT} from '@/config/image.js';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import uniPopupOne from '@/components/uni-popup/uni-popup.vue';
import status from '../../utils/status.js';
import hTimePicker from "@/components/h-timePicker/h-timePicker.vue";
import { LOGIN} from '@/config/router.js';
// import Word from "./components/text.vue";
import {
		submitData
	} from '@/api/submit.js'
import {
		getStorage,setStorage
	} from '@/utils/storage.js'
	import {
		UPLOAD
	} from '@/config/api.js';
export default {
	components: {
		uniPopup,
		 hTimePicker,
		 uniPopupOne
		 // Text
	},
	data() {
		return {
			bohaiApplyHeight:'2350rpx',
			dataList:{
				phone:'',
				cardBeforeImg:'',
				cardAfterImg:'',
				bookletIndexImg:'',
				bookletThisImg:'',
				bookletMainImg:'',
				marriageCertificateType:'' ,//结婚证
				divorce:'',
				marriageIncomeProof:'',
				incomeProof:'',
				thisRunningWater:[
					
				],
				spouseRunningWater:[
					
				]
			},
			dataL:{
				cardBeforeImg:'',
				cardAfterImg:'',
				bookletIndexImg:'',
				bookletThisImg:'',
				bookletMainImg:'',
				marriageCertificateType:'' ,//结婚证
				divorce:'',
				marriageIncomeProof:'',
				incomeProof:'',
				thisRunningWater:[
					
				],
				spouseRunningWater:[
					
				]
			},
			img: BOHAI,
			rightimg:RIGHT,
			// cityPickerValueDefault: [0, 0, 1],
			//                 pickerText: '',
			showCaledar:false,
			bohaiArray: ['中国', '美国', '巴西', '日本'],
			bohaiIndex: 0,
			repayArray: ['等额本息', '等额本金'],
			repayIndex: 0,
			loanArray: ['商业贷款', '公积金贷款','组合贷款'],
			loanIndex: 0,
			marriageCurrent:0,
			marriageItems: [
				{
					value: '未婚',
					name: '未婚'
				},
				{
					value: '已婚',
					name: '已婚'
				},
				
				{
					value: '离异',
					name: '离异'
				}
			],
			marryPaths: '',
			thisCurrent:0,
			thisItems: [
				{
					value: '企业职员',
					name: '企业职员'
				},
				{
					value: '法人',
					name: '法人'
				}
			],
			spouseCurrent:0,
			spouseItems: [
				{
					value: '企业职员',
					name: '企业职员'
				},
				{
					value: '法人',
					name: '法人'
				}
			],
			value:'',
			startTime:'',
			closeTime:'',
			agree:false
			
			
		};
	},
	methods: {
		
		// openAddres() {

		//                 this.$refs.simpleAddress.open();
		//             },
		//             onConfirm(e) {

		// 				var b=e.label.split('-')
		// 				// console.log(e)

		//                 this.pickerText = b[0]+' '+ b[1]+' '+b[2]
		//             },
		open() {
			this.$refs.popup.open();
		},
		cancel(e) {
			this.$refs.popup.close();
			if(e == 'ok'){
				this.agree=true
			}
			if(e == 'no'){
				this.agree=false
			}
			console.log(this.agree)
		},
		poponeclick(){
			this.$refs.popupone.close();
		},
		popchange(e) {
			console.log('是否打开:' + e.show);
		},
		change(e) {
			console.log('是否打开:' + e.show);
		},
		bohaiBindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.bohaiIndex = e.target.value;
			
		},
		repayBindPickerChange: function(e) {
			
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.repayIndex = e.target.value;
			_self.dataList.refundType = this.repayArray[e.target.value]
		},
		loanBindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.loanIndex = e.target.value;
			_self.dataList.loanType = this.loanArray[e.target.value]
		},
		agre() {
			_self.agree=!this.agree
		},
		submit() {
			// uni.navigateTo({
			// 	url:CHOOSEBUSSINESS
			// })
			// console.log(this.agree)
			if(!this.agree) {
				uni.showToast({
				    title: '请阅读用户信息授权协议',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataList.name) {
				uni.showToast({
				    title: '请输入名字',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataList.phone) {
				uni.showToast({
				    title: '请输入手机号',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataList.certificateCode) {
				uni.showToast({
				    title: '请输入身份证',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataList.loanAmount) {
				uni.showToast({
				    title: '请输入贷款金额',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataList.loanPeriod) {
				uni.showToast({
				    title: '请输入贷款年限',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataL.cardBeforeImg) {
				uni.showToast({
				    title: '请上传身份证正面',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataL.cardAfterImg) {
				uni.showToast({
				    title: '请上传身份证背面',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataL.bookletIndexImg) {
				uni.showToast({
				    title: '请上传户口本首页',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataL.bookletThisImg) {
				uni.showToast({
				    title: '请上传户口本户主页',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataL.bookletMainImg) {
				uni.showToast({
				    title: '请上传户口本本人页',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(!this.dataList.faceTime) {
				uni.showToast({
				    title: '请输入正确的预约时间',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			if(this.marriageCurrent ===1){
				if(!this.dataList.marriageName) {
					uni.showToast({
					    title: '请输入配偶姓名',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
				if(!this.dataList.marriageCertificateCode) {
					uni.showToast({
					    title: '请输入配偶身份证',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
				if(!this.dataL.marriageCertificateType) {
					uni.showToast({
					    title: '请上传结婚证',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
				if(this.spouseCurrent === 0){
					if(!this.dataL.marriageIncomeProof) {
						uni.showToast({
						    title: '请完善信息后提交',
						    duration: 2000,
							icon:'none'
						});
						return;
					}
					if(!this.dataL.spouseRunningWater) {
						uni.showToast({
						    title: '请完善信息后提交',
						    duration: 2000,
							icon:'none'
						});
						return;
					}
				}
				if(this.spouseCurrent === 1){
					if(!this.dataL.marriageIncomeProof) {
						uni.showToast({
						    title: '请完善信息后提交',
						    duration: 2000,
							icon:'none'
						});
						return;
					}
					if(!this.dataL.spouseRunningWater) {
						uni.showToast({
						    title: '请完善信息后提交',
						    duration: 2000,
							icon:'none'
						});
						return;
					}
				}
				
			}
			if(this.marriageCurrent ===2){
				if(!this.dataL.divorce) {
					uni.showToast({
					    title: '请上传离婚证',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
				
			}
			// console.log(this.thisCurrent)
			if(this.thisCurrent === 0){
				if(!this.dataL.incomeProof) {
					uni.showToast({
					    title: '请完善信息后提交',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
				if(!this.dataL.thisRunningWater) {
					uni.showToast({
					    title: '请完善信息后提交',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
			}
			// console.log(this.thisCurrent)
			if(this.thisCurrent === 1){
				if(!this.dataL.incomeProof) {
					uni.showToast({
					    title: '请完善信息后提交',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
				if(!this.dataL.thisRunningWater) {
					uni.showToast({
					    title: '请完善信息后提交',
					    duration: 2000,
						icon:'none'
					});
					return;
				}
			}
			
			submitData(this.dataList).then(res=>{
				// uni.showToast({
				// 				title:'提交成功',
				// 			})
				// 			setTimeout(function(){
				// 				setStorage('isLogin', false)
				// 				uni.reLaunch({
				// 					url: LOGIN,
				// 				})
				// 			},3000)
				this.$refs.popupone.open();
				
				
				
			})
			console.log(this.dataList)
		},
		marriageRadioChange: function(evt) {
			for (let i = 0; i < this.marriageItems.length; i++) {
				if (this.marriageItems[i].value === evt.target.value) {
					this.marriageCurrent = i;
					_self.dataList.marriageState = evt.target.value
					if (i == 0){
						_self.bohaiApplyHeight = '2460rpx'
					}
					if (i == 1){
						_self.bohaiApplyHeight = '3100rpx'
					}
					if (i == 2){
						_self.bohaiApplyHeight = '2500rpx'
					}
					
					
					break;
				}
			}
		},
		spouseRadioChange: function(evt) {
			for (let i = 0; i < this.spouseItems.length; i++) {
				if (this.spouseItems[i].value === evt.target.value) {
					this.spouseCurrent = i;
					_self.dataList.marriageWorkNature = evt.target.value
					break;
				}
			}
		},
		thisRadioChange: function(evt) {
			for (let i = 0; i < this.thisItems.length; i++) {
				if (this.thisItems[i].value === evt.target.value) {
					this.thisCurrent = i;
					_self.dataList.workNature = evt.target.value
					break;
				}
			}
		},
		// 选择照片
		chooseimage: function(e) {
			var _this = this;
			uni.chooseImage({
				count: 9, // 默认9
				sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success: function(res) {
					// 返回选定照片的本地文件路径列表，marryPath可以作为img标签的src属性显示图片
					var imgFiles = res.tempFilePaths[0]
					
					
					uni.uploadFile({
					                            // 需要上传的地址
					                            url:UPLOAD,
					                            // filePath  需要上传的文件
					                            filePath: imgFiles,
					                            name: 'file',
					                            success: (res1) =>{
					                                // 显示上传信息
					        //                         console.log(res1)
													// console.log(_self)
													// console.log(e)
													// console.log(_self.dataList[e])
													// console.log(res1.data )
													let w=JSON.parse(res1.data)
													if(e ==='thisRunningWater'||e ==='spouseRunningWater') {
														_self.dataL[e].push(imgFiles);			
														_self.dataList[e].push(w.data);
														
													} else{
														_self.dataList[e] = w.data;
														_self.dataL[e] = imgFiles;
													}
													
													
					                            }
					                        });
					
				}
			});
		},
		startChangeTime(e){
			console.log(e)
			_self.startTime = e
		},
		closeChangeTime(e){
			console.log(e)
			_self.closeTime = e
			_self.dataList.faceTime = this.startTime +'~'+this.closeTime
		},
		getTime:function(){
		
		var date = new Date(),
		year = date.getFullYear(),
		month = date.getMonth() + 1,
		day = date.getDate(),
		hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
		minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
		second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
		month >= 1 && month <= 9 ? (month = "0" + month) : "";
		day >= 0 && day <= 9 ? (day = "0" + day) : "";
		var timer = year  + month + day ;
		return timer;
		},
		// changeTime(e){
		// 	console.log(e)
		// },
	},
	onLoad() {
		_self = this;
		_self.dataList.phone=getStorage('phone')?getStorage('phone'):''
		
		_self.dataList.refundType = this.repayArray[0]
		_self.dataList.loanType = this.loanArray[0]
		
		_self.dataList.marriageState = '未婚'
		_self.dataList.marriageWorkNature = '企业职员'
		_self.dataList.workNature = '企业职员'
		console.log(this.dataList)
		
		
		// var data=this.getTime()
		
		
	}
};
